.Beat {
  width: 24px;
  height: 24px;
  border-radius: 50%;
  margin: 0 10px;

  background: #646464;
  background-image: -webkit-radial-gradient(top, #616161 0%, #616161 20%, #535353 60%);
  background-image: -o-radial-gradient(top, #616161 0%, #616161 20%, #535353 60%);
  background-image: radial-gradient(top, #616161 0%, #616161 20%, #535353 60%);
  -webkit-box-shadow: 0 0 4px #1C1C17;
          box-shadow: 0 0 4px #1C1C17;

  -webkit-transition: all 0.1s ease;

  -o-transition: all 0.1s ease;

  transition: all 0.1s ease;
}

.Beat--current {
  -webkit-box-shadow: 0 0 8px #dd8836;
          box-shadow: 0 0 8px #dd8836;
  background-image: -webkit-radial-gradient(top, #DD8836 0%, #DD8836 20%, #CA7B32 60%);
  background-image: -o-radial-gradient(top, #DD8836 0%, #DD8836 20%, #CA7B32 60%);
  background-image: radial-gradient(top, #DD8836 0%, #DD8836 20%, #CA7B32 60%);
}



